@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: Roboto, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.contents {
  background-color: #2196f3;
  text-align: center;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-weight: bold;
  color: whitesmoke;
  margin-top: 2%;
  font-size: larger;
  line-height: 300%;
}
body {
  background-color:#222;
}
.footer {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    background-color: #312f2f;
    text-align: center;
    display: flex;
    justify-content: space-evenly;
}

.footer a {
    color: whitesmoke;
    font-family: 'Roboto', sans-serif;
    line-height: 250%;
}


.tester {
    text-align: left;
    font-weight: bold;
    font-size: larger;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #90caf9
}
.wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;  
}
.parent {
    margin: 0 1rem;
}

.MuiButton-label {
  text-transform: capitalize;
}
.MuiFormGroup-root {
   color: white 
}
.MuiButton-containedPrimary:hover {
  background-color: whitesmoke !important;
}
.MuiInputBase-root {
  color: #90caf9 !important;
}
.MuiFormLabel-root.Mui-focused {
  color: #90caf9 !important ;
  font-size: medium !important;
}

.MuiInputBase-input {
  color: white !important;
  font-family: Roboto, sans-serif;
}

.MuiButtonGroup-root {
  background-color: #90caf9 ;
  box-shadow:  12px 12px 48px #080808, 
             -12px -12px 48px #121212;
}

.MuiSvgIcon-fontSizeSmall {
  color: #90caf9;
}

.MuiInputLabel-root {
  color: #90caf9;
}

.MuiOutlinedInput-notchedOutline {
  border-color: #90caf9 !important;
}

.MuiFormLabel-root {
  color: white !important;
  font-size: small !important;
}
.MuiRadio-root {
  color: white !important;
}
main { margin: auto; max-width: 960px; }

.table-container {
  margin: 10px;
  padding: 0;
  border: 1px solid #e5e5e5;
  border-radius: 5px;
}

tr.expandable > td {
  background-color: linear-gradient(145deg, #242525, #1f2020) !important;
  box-shadow: 5px 5px 10px #1d1e1e, 
  -5px -5px 10px #272828;
  padding: 0;
  overflow-wrap: anywhere;
}

tr.expandable > td > .inner {
  margin: 15px;
  overflow-wrap: anywhere !important;
}

h4 {
  text-align: center;
}

.MuiAccordionSummary-root {
  background-color: #1a1a1a !important;
  border-radius: 10px !important;
}

.MuiAccordion-root .MuiSvgIcon-root {
  fill: white
}
.MuiTableCell-stickyHeader {
    background-color: #191919 !important;
}

.MuiTableCell-head {
    color: white !important;
}

.MuiTable-root  {
    background-color: #222222 !important;
}


.MuiTableCell-root, .MuiTableCell-body, .MuiTableCell-alignLeft {
    color: 'wheat' !important;
    border-bottom: black !important;
}

.MuiTableSortLabel-root.MuiTableSortLabel-active {
    color: 'white' !important;
}


.MuiTableSortLabel-root.MuiTableSortLabel-active {
    color: white !important;
}

.MuiTableSortLabel-root:hover {
    color: white !important;
}

.MuiTableSortLabel-root.MuiTableSortLabel-active.MuiTableSortLabel-root.MuiTableSortLabel-active .MuiTableSortLabel-icon {
    color: white !important;
}

.MuiSvgIcon-root {
    fill: 'white' !important;
}
